.logostrip {
  display: flex;
  justify-content: center;
  justify-items: center;
  flex-wrap: wrap;
  gap: 50px;
  max-width: 90vw;
  margin-bottom: 25px;
}
.logo {
  object-fit: contain;
}

.personContainer {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  padding: 15px 0;
  width: 100%;
  max-width: 380px;
  min-height: 340px;
}

.person-img {
  width: 150px;  
}

.team-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 6vw;
}
