#videoContainer2 {
  position: relative;
  width: 100vw;
  height: 500px;

  text-align: left;
  overflow: hidden;
  z-index: 10;
}

#videoContainer3 {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;
  display: flex;
  align-items: center;
}

#videoContainer::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;

  /* -webkit-filter: blur(20px);
    -moz-filter: blur(20px);
    -o-filter: blur(20px);
    -ms-filter: blur(20px);
    filter: blur(20px); */
  background: rgba(0, 0, 0, 0.2);
}

#videoContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
}

#video {
  width: inherit;
  height: inherit;
  object-fit: cover;
}

#slogan {
  overflow: hidden;
  z-index: 1;
  color: white;
  font-weight: 600;
}
