
.App {
  text-align: center;
}

.App-logo {
  width: 50vmin;
  pointer-events: none;
}

.wave {
  fill: url(#gradient);
  opacity: 100%;
}

.main {
  width: 100%;
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 100;
}
.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 95%;
  padding: 40px 0;
}
.image-section {
  display: grid;
  grid-template-columns: auto auto;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10vw;
  margin: 3vw 0;
}

.media-description {
  max-width: 500px;
}
.section-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 90%;
  margin: 2vw;
}
.appbar {
  position: fixed;
  justify-content: center;
  top: 50px;
  z-index: 100;
}

.wave {
  position: relative;
  margin-bottom: -40px;
}
img {
  max-width: 90vw;
}
.media-img {
  border-radius: 20px;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
  max-height: 300px;
  max-width: 100vw;
}
.video-wrapper {
  position: relative;
  width: 100%;
  padding-bottom: 56.25%; /* 16:9, for an aspect ratio of 1:1 change to this value to 100% */ 
}

iframe {
  width: 100%;
  aspect-ratio: 16 / 9;
  height: auto;
  max-width: 1000px;
  margin: 20px 0;
}

.roadmap-container {
  display: grid;
  width: 100%;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, minmax(auto, 300px));
  gap: 40px;
}

