.stat-box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  gap: 10px;
  margin: 1vw;
  width: 200px;
}

.stat-text {
  color: white;
}

.stat-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  gap: 6vw;
  flex-wrap: wrap;
}

.stat-body {
    display: flex;
    max-width: 200px;
    justify-content: center;
    align-items: center;
}
